
.connect {
  position: relative;
  left: 10%;
  transform: translate(-20%, 0%);
  padding-top: 90px;
  padding-bottom: 90px;
  margin: 0 auto;
  width: 100%;
  max-width: 1100px;

  .connect-inner {
      margin: 0 auto;
      width: 100%;
      max-width: 1100px;
      display: flex;
      flex-direction: row;
      align-items: center;

      .connect-form {
          .form {
              width: 90%;
              margin-top: 20px;
              form {
                  display: block;
                  margin-top: 0em;

                  .email {
                      width: 100%;
                      flex-direction: row;
                      margin-bottom: 15px;
                      label {
                          color: #ffffff;
                          font-size: 12px;
                          margin-bottom: 5px;
                      }
                      div {
                          position: relative;
                          width: 100%;
                          flex-direction: row;
                          margin-bottom: 15px;
                      }
                      input {
                          padding-right: 150px;
                          width: 100%;
                          max-width: 100%;
                          border: 1px solid #8892b0;
                          color: #30343f;
                          margin-top: 5px;
                          border-radius: 5px;
                          padding: 12px;
                          font-size: 14px;
                          resize: vertical;
                      }
                  }

                  .message {
                      width: 100%;
                      flex-direction: row;
                      margin-bottom: 15px;
                      label {
                          color: #ffffff;
                          font-size: 12px;
                          margin-bottom: 5px;
                      }
                      div {
                          width: 100%;
                          flex-direction: row;
                          margin-bottom: 15px;
                      }
                      textarea {
                          width: 100%;
                          max-width: 100%;
                          border: 1px solid #8892b0;
                          color: #30343f;
                          margin-top: 5px;
                          border-radius: 5px;
                          padding: 12px;
                          font-size: 14px;
                          resize: vertical;
                      }
                  }
              }
              .btn-send {
                  cursor: pointer;
                  display: block;
                  background-color: #e6284a;
                  color: #ffffff;
                  border-radius: 5px;
                  padding: 12px 20px;
                  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
                  -webkit-transition: all 0.25s cubic-bezier(0.645, 0.045, 0.355, 1);
                  transition: all 0.25s cubic-bezier(0.645, 0.045, 0.355, 1);
                  font-size: 16px;
                  border: 0;
                  width: 200px;
                  margin-left: auto;
              }
          }
      }

      .connect-title {
          color: #fafaff;
          margin-bottom: 45px;
          font-size: 52px;
          line-height: 54px;
          margin-bottom: 15px;
          text-align: left;
          font-weight: 600;
      }

      .connect-description {
          font-size: 22px;
          margin: 0px;
          font-size: 18px;
          line-height: 24px;
          color: rgb(250, 250, 255);
          font-weight: 300;
      }

      .connect-image {
          margin: 0 20px;
          .image {
              position: relative;
              overflow: hidden;
              max-width: 400px;
              margin-left: auto;
              margin-right: auto;

              .png {
                  width: 100%;
                  img {
                      // position: absolute;
                      top: 0px;
                      left: 0px;
                      width: 100%;
                      height: 100%;
                      object-fit: cover;
                      object-position: center center;
                  }
              }
          }
      }
  }
}