
.stack {
  position: relative;
  left: 10%;
  transform: translate(-20%, 0%);
  padding-top: 90px;
  padding-bottom: 90px;
  margin: 0 auto;
  width: 100%;
  max-width: 1100px;
  .stack-title {
      color: #fafaff;
      margin-bottom: 45px;
      font-size: 52px;
      line-height: 54px;
      margin-bottom: 15px;
      text-align: left;
      font-weight: 600;
  }

  .stack-wrap {
      display: flex;
      .stack-dev {
          flex-grow: 1;
          text-align: center;
          background-color: #1b1b1b;

          &:nth-child(1) {
              margin-right: 10px;
              border: 1px solid #1b1b1b;
              border-radius: 10px;
              padding: 20px;
          }
          &:nth-child(2) {
              margin-left: 10px;
              border: 1px solid #1b1b1b;
              border-radius: 10px;
              padding: 20px;
          }

          .stack-subtitle {
              color: #e6284a;
              margin-bottom: 1rem;
              width: 100%;
              text-align: center;
              line-height: 1.4;
              font-size: 1.2rem;
              font-weight: 700;
          }

          .stack-subdesc {
              margin-bottom: 3rem;
              text-align: center;
              line-height: 1.4;
              font-size: 1rem;
          }

          .stack-list {
              box-sizing: border-box;
              outline: 0;
              margin: 0;
              padding: 0;
              list-style: none;
              .stack-elem {
                  text-align: center;
                  line-height: 1.4;
                  margin-bottom: 0.4rem;
              }
          }
      }
  }
}
